
import React, { Component } from 'react'
import { bindActionCreators } from 'redux'
import { connect } from 'react-redux'
import { message } from 'antd'

import Header from './header'
import Footer from './footer'
import LeftNav from './leftNav'
import RightAside from './rightAside'
import TabList from './tabList'
import Extra from './extra'
import 'antd/dist/antd.less'
import '../../style/base.less'
import { getUserMessage } from '../../ajax/user'
//connect Statedispatchprops
//bindActionCreatorsactiondispatch
// mapDispatchToProps
@connect(
    (dispatch) => ({ actions: bindActionCreators({}, dispatch) })
)
//react class
export default class App extends Component {
  //  
  constructor(props, context) {
    super(props)
    //State
    this.state = {
      pageHeight: 0,
      isLeftNavMini: false,   // mini

    }

    this.isLeftNavMini = this.isLeftNavMini.bind(this)
  }


  // dom
    //this.getDOMNode()dom
  componentDidMount() {
    // antdmessage 
    message.config({
      duration: 3,
    })
  }
//state
  componentWillMount() {
    // mini
    if (sessionStorage.getItem('isLeftNavMini') == 'false') {
      this.setState({
        isLeftNavMini: false,
      })
    } else {
      this.setState({
        isLeftNavMini: true,
      })
    }

  }

  // mini
  isLeftNavMini(val) {
    this.setState({
      isLeftNavMini: val,
    }, () => {
      sessionStorage.setItem('isLeftNavMini', val)
    })
  }
//reactdomdiffdomstate
  render() {
    const { location, children } = this.props
    return (
      <div id="container" className="effect easeInOutBack mainnav-lg aside-bright">
        <Header />
        <div className="boxed">
          <div className={this.state.isLeftNavMini ? 'boxed boxed-mini' : 'boxed'}>
            <div id="content-container" className="content-container">
              <div id="page-content">
                {/*<TabList />*/}
                {children}
              </div>
            </div>
          </div>
          <LeftNav
            location={location}
            leftNavMode={this.isLeftNavMini}
          />
          <RightAside />
        </div>
        <Footer />
        <Extra />
      </div>
    )
  }
}



