import {
  routerReducer as routing,
} from 'react-router-redux'
import {
  combineReducers,
} from 'redux'

import tabListResult from './tabList'

// house
import {
  houseCheckSearchResult,
  houseCheckSearchQuery,
  houseDetailResult,
} from './house'

import {
  loginResponse,
   logout,
} from './login'

//reducerstateaction
// statereducerreduceraction
const rootReducer = combineReducers({
  routing,
  config: (state = {}) => state,
  //ucUser: (state = {}) => state,
  tabListResult,
    logout,
  loginResponse,

  houseCheckSearchResult,
  houseCheckSearchQuery,
  houseDetailResult,


});

export default rootReducer;



